import React from 'react';
import Loader from '../Loader';
import sanitizeHtml from 'sanitize-html';
import CardEL from '../shared/CardEL';


import { useParams } from 'react-router-dom';

import { GET_AUTHOR_INFO } from '../../graphQl/queries';
import { useQuery } from '@apollo/client';

import { Avatar, Container, Grid, Typography } from '@mui/material';

const AuthorPage = () => {

    const { slug } = useParams();
    const { loading, data, error } = useQuery(GET_AUTHOR_INFO, {variables: { slug },});
    console.log({ loading, data, error });
    


    if (loading) return <Loader />;
    if (error) return <h1>{error}</h1>; 

    if (data) return (
       <Container maxWidth='lg'>
            <Grid container mt={10}>
                <Grid item xs={12} display='flex' flexDirection='column' alignItems='center'>
                    <Avatar src={data.author.avatar.url} sx={{ width: 250, height: 250,}} />
                    <Typography component='h3' variant='h5' fontWeight={700} mt={4}>
                        {data.author.name}
                    </Typography>
                    <Typography component='p' variant='h5' color='text.secondary' mt={2}>
                        {data.author.field}
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <div dangerouslySetInnerHTML={{ __html: sanitizeHtml(data.author.description.html), }}></div>
                </Grid>
                <Grid item xs={12} mt={6}>
                    <Typography component='h3' variant='h5' fontWeight={700}>مقالات {data.author.name}</Typography>
                    <Grid container spacing={2} mt={2}>
                        { data.author.posts.map(post => (
                            <Grid item xs={12} sm={6} md={4} key={post.slug}>
                                <CardEL title={post.title} slug={post.slug} coverPhoto={post.coverPhoto}/>
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
            </Grid>
       </Container>
    );
};

export default AuthorPage;